.features-list {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.features-list__search-panel {
  padding: 16px 24px;
}

.features-list__list {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 56px;
  overflow-y: scroll;
  padding: 0 24px;
}

.features-list__no-content-container {
  width: 100%;
  margin: 0 auto;
  padding-top: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.features-list__loading {
  padding-left: 8px;
}
