.feature-item-separator {
  width: 100%;
  padding-left: 10px;
  border-radius: var(--ring-border-radius-small);
  background-color: #F2F2F2;
  height: 24px;
  line-height: 24px;
  color: #A3A3A5;
  text-transform: uppercase;
  font-size: var(--ring-font-size-smaller);
  margin-top: 8px;
  margin-bottom: 1px;
}

.feature-item-separator:first-child {
  margin-top: 4px;
}
