.features-list-panel {
  width: 100%;
  border: 1px solid var(--ktor-borders-color);
  padding-right: 4px;
  box-shadow: var(--ktor-island-box-shadow);
  min-height: var(--ktor-max-island-height);
  max-height: var(--ktor-max-island-height);
  position: relative;
}

.features-list-panel__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: hidden;
}

.features-list-panel__description {
  padding: 24px 32px 0 32px;
}

.features-list-panel__list {
  position: absolute;
  top: 46px;
  left: 0;
  right: 0;
  bottom: 64px;
}

.features-list-panel__footer {
  box-sizing: border-box;
  padding: 4px 24px;
  width: 100%;
  min-height: 64px;
  flex: 0 1 64px;
  background: #F0673E;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
}

.features-list-panel__footer-label {
  color: white;
  white-space: nowrap;
}
