.added-features {
  --ktor-added-features-list-max-height: calc(var(--ktor-max-island-height) - 64px);

  width: 100%;
  min-height: 0;
  max-height: 0;
  overflow: hidden;
  top: var(--ktor-added-features-list-max-height);
  background-color: white;
  position: absolute;
  transition: top 0.3s, min-height 0.3s, max-height 0.3s;
  z-index: 1;
}

.added-features_shown {
  min-height: var(--ktor-added-features-list-max-height);
  max-height: var(--ktor-added-features-list-max-height);
  top: 0;
}

.added-features__container {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: hidden;
  min-height: var(--ktor-added-features-list-max-height);
  max-height: var(--ktor-added-features-list-max-height);
}

.added-features__search {
  margin-top: 12px;
  padding-right: 24px;
  padding-left: 24px;
}

.added-features__list {
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0;
}

.added-features__header {
  box-sizing: border-box;
  padding: 4px 24px;
  width: 100%;
  min-height: 64px;
  flex: 0 1 64px;
  background: #F0673E;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.added-features__header-text {
  color: white;
}

.added-features__header-icon {
  color: white;
  line-height: 20px;
  cursor: pointer;
}

.added-features__no-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}