.remove-feature-confirmation {}

.remove-feature-confirmation__content {
  padding: calc(2 * var(--ring-unit)) calc(2 * var(--ring-unit)) 0 calc(2 * var(--ring-unit));
  max-height: 350px;
  width: 350px;
  overflow: auto;
  font-size: var(--ring-font-size-smaller);
}

.remove-feature-confirmation__section {
  margin-bottom: calc(2 * var(--ring-unit));
}

.remove-feature-confirmation__label {
  display: flex;
  align-items: flex-start;
}

.remove-feature-confirmation__label-text {
  margin-top: 2px;
}

.remove-feature-confirmation__list-item {
  margin: 6px -3px;
  --wt-color-primary-light-theme: var(--wt-color-dark);
}

.remove-feature-confirmation__footer {
  margin: 0;
  padding: 14px calc(2 * var(--ring-unit)) calc(2 * var(--ring-unit)) calc(2 * var(--ring-unit));
  border-top: 1px solid var(--ktor-borders-color);
}
