$header-height: 70px;

.header {
  height: $header-height;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  border-bottom: 1px solid var(--ktor-borders-color);
  background-color: rgba(256, 256, 256, 0.9);
  z-index: 1;
  border-bottom: 1px solid var(--ktor-borders-color);
}

.header__container {
  height: 70px;
  margin: 0 auto;
  max-width: 1276px;
  padding-left: 22px;
  padding-right: 22px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header__logo {
  display: flex;
  margin-right: 8px;
}

.header__title {
  white-space: nowrap;
  font-size: 23px;
}

.header__wizard-steps {
  display: flex;
  flex-direction: row;
  margin-left: 24px;
  margin-top: 3px;
  align-items: center;
}

.header__wizard-step {
  padding: 8px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__wizard-step:not(.header__wizard-step_disabled):hover {
  color: #323236;
  cursor: pointer;
}

.header__wizard-step_active, .header__wizard-step_active:hover {
  color: #6578FC;
  font-weight: 500;
}

.header__wizard-step-number {
  width: 20px;
  height: 20px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 11px;
  line-height: 20px;
  font-weight: 500;
  background: rgba(39, 40, 44, 0.6);
  color: white;
  padding-left: 0.1px;
  margin-top: 1px;
  margin-right: 4px;
}

.header__wizard-step:not(.header__wizard-step_disabled):hover > .header__wizard-step-number {
  background: #323236;
}

.header__wizard-step_active > .header__wizard-step-number,
.header__wizard-step_active:hover > .header__wizard-step-number {
  background: #6578FC;
}

.header__spin-icon {
  animation: spin 1s linear infinite;
}

.header__arrow {
  transform: rotate(90deg);
  margin: 2px 4px 0 4px;
  color: var(--wt-color-dark-40);
}

.content {
  margin-top: $header-height;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
