.feature-item {
  width: 100%;
  padding: 8px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: var(--ring-border-radius);
  justify-content: space-between;
  align-items: center;
  border: solid 1px transparent;

  --wt-color-dark: var(--ktor-primary-cold-background-color);
  --wt-color-dark-70: var(--ktor-secondary-cold-background-color);
}

.feature-item:hover {
  background-color: var(--ktor-pale-cold-background-color);
}

.feature-item.feature-item_was-added {
  --wt-color-dark: var(--ktor-primary-warm-background-color);
  --wt-color-dark-40: var(--ktor-primary-warm-background-color);
  --wt-color-dark-70: var(--ktor-secondary-warm-background-color);
}

.feature-item.feature-item_was-added:hover {
  background-color: var(--ktor-pale-warm-background-color);
}

.feature-item.feature-item_selected {
  border-color: var(--wt-color-dark-70);
  background-color: var(--ktor-pale-cold-background-color);
}

.feature-item.feature-item_selected.feature-item_was-added {
  background-color: var(--ktor-pale-warm-background-color);

  --wt-color-dark: var(--ktor-primary-warm-background-color);
  --wt-color-dark-40: var(--ktor-primary-warm-background-color);
  --wt-color-dark-70: var(--ktor-secondary-warm-background-color);
}

.feature-item__left-side {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0 8px 0 2px;
}

.feature-item__name {
  font-weight: 600;
}

.feature-item__description {
  line-height: var(--ring-line-height);
}

.feature-item__button {
  visibility: hidden;
}

.feature-item:hover .feature-item__button,
.feature-item__button_shown {
  visibility: visible;
}
