.form-toggle-link {
  cursor: pointer;
  color: white;
  font-weight: 500;
  font-size: 15px;
  position: relative;
  line-height: 48px;
  white-space: nowrap;
}

.form-toggle-link:hover {
  text-decoration: underline;
}

.form-toggle-link:focus {
  outline: none;
  text-decoration: underline;
}

.form-toggle-link__arrow {
  position: relative;
  top: 7px;
  left: 4px;
  transition: transform 250ms;
  cursor: pointer;
  display: inline-block;
  fill: white;
}

.form-toggle-link__arrow_up {
  transform: rotate(180deg);
}