.project-form {
  width: 100%;
  border: 1px solid var(--ktor-borders-color);
  box-shadow: var(--ktor-island-box-shadow);
  margin-bottom: 48px;
  --wt-color-primary-light-theme-80: #ff7936;
}

.project-form__project-name {
  padding: 24px 32px 0 32px;
}

.project-form__footer {
  height: 64px;
  padding: 14px 32px;
  background-color: var(--ktor-primary-cold-background-color);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

