.project-settings {
  box-sizing: border-box;
  width: 100%;
  padding: 0 32px;
  max-height: 0;
  overflow: hidden;
  transition: max-height .3s, padding .3s;
  background-color: var(--ktor-primary-cold-background-color);
  --wt-color-primary-light-theme: var(--ktor-primary-warm-background-color);
  --wt-color-primary-dark-theme: var(--ktor-primary-warm-background-color);
  --wt-color-primary-light-theme-80: var(--ktor-secondary-warm-background-color);
  --wt-color-primary-dark-theme-80: var(--ktor-secondary-warm-background-color);
}

.project-settings_expanded {
  padding: 4px 32px 32px 32px;
  max-height: 1000px;
}

.project-settings__row {
  margin-bottom: 12px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.project-settings__row:first-child {
  padding-top: 8px;
}

.project-settings__row:first-child > .project-settings__row-label {
  padding-top: 0;
}

.project-settings__row:last-child {
  margin-bottom: 12px;
}

.project-settings__row-label {
  width: 150px;
  padding-right: 16px;
  padding-top: 5px;
}

.project-settings__row-input {
  flex: 1 0 150px;
}

.project-settings__row-input > div:first-child {
  background: rgba(255, 255, 255, 0.05);
}

.project-settings__row-input div[data-test="select"] > div > div {
  color: var(--wt-color-white) !important;
}

.project-settings__row-input div[data-test="select"] > div > svg {
  fill: var(--wt-color-white) !important;
}

.project-settings__row-input input {
  color: var(--wt-color-white) !important;
}

.project-settings__note {
  position: relative;
  top: -2px;
  color: var(--wt-color-white-60);
}

.project-settings__checkbox {
  flex: 1 0 150px;
  --wt-color-primary-dark-theme: var(--wt-color-white);
}
.project-settings__checkbox svg {
  fill: var(--wt-color-grey);
}
